@keyframes lds-1 {
  0% {
    top: 25px;
    height: 160px;
  }
  50% {
    top: 66px;
    height: 80px;
  }
  100% {
    top: 66px;
    height: 80px;
  }
}
@keyframes lds-2 {
  0% {
    top: 35px;
    height: 142px;
  }
  50% {
    top: 66px;
    height: 81px;
  }
  100% {
    top: 66px;
    height: 81px;
  }
}
@keyframes lds-3 {
  0% {
    top: 46px;
    height: 120px;
  }
  50% {
    top: 66px;
    height: 81px;
  }
  100% {
    top: 66px;
    height: 81px;
  }
}

.loading__box {
  display: inline-block;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  &-lds {
    width: 200px;
    height: 200px;
    position: relative;
    transform: translateZ(0) scale(1);
    backface-visibility: hidden;
    transform-origin: 0 0;
    div {
      position: absolute;
      box-sizing: content-box;
      width: 20px;
      &:nth-child(1) {
        left: 35px;
        background: #85a2b6;
        animation: lds-1 1s cubic-bezier(0, 0.5, 0.5, 1) infinite;
        animation-delay: -0.2s;
      }
      &:nth-child(2) {
        left: 85px;
        background: #bbcedd;
        animation: lds-2 1s cubic-bezier(0, 0.5, 0.5, 1) infinite;
        animation-delay: -0.1s;
      }
      &:nth-child(3) {
        left: 135px;
        background: #dce4eb;
        animation: lds-3 1s cubic-bezier(0, 0.5, 0.5, 1) infinite;
        animation-delay: undefineds;
      }
    }
  }
  &.fixed {
    position: fixed;
    top: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    background-color: #fff;
    img {
      width: 200px;
      height: 150px;
    }
  }
}

@media screen and (max-width: 600px) {
  .fixed {
    .loading__box-lds {
      zoom: 0.3;
    }
  }
}

@primary-color: red;@highlight-color: red;